.verticalLine {
  z-index: 15;
  background-color: rgba(231, 231, 231, 0.5);
  width: 1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 11.6%;
}
.slideNumberBox {
  z-index: 20;
  background-color: #f8c326;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  padding: 4px 12px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 8%;
  left: -15px;
}
.bannerContent {
  z-index: 20;
  color: #fff;
  width: 45%;
  position: absolute;
  top: 8%;
  left: 14%;
}

.bannerContent h2 {
  margin-bottom: 1.5rem;
}

.bannerContent p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 2rem;
  color: #fff;
  max-width: 500px;
}

.slideContainer {
  position: relative;
  width: 100%;
  height: 100vh;
}

/* Gradient Overlays */
.slideContainer::before,
.slideContainer::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 120px;
  pointer-events: none;
  z-index: 10;
}

.slideContainer::before {
  top: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), transparent);
}

.slideContainer::after {
  bottom: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6), transparent);
}

.bannerLinks {
  gap: 2rem;
  font-size: 14px;
  display: flex;
  position: absolute;
  bottom: 15%;
  left: 14%;
  color: #fff;
  font-weight: 600;
  z-index: 20;
}
.bannerLink {
  color: #fff;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 8px;
  border-bottom: 2px dotted #ffc100;
  font-size: 14px;
}
.headingContainer {
  position: relative;
  background: #f6f6f6;
  width: 100%;
  padding: 4rem 10rem 0;
}
.plusIcon {
  color: #cba632;
  font-size: 32px;
  border: 1px solid #16344e;
  border-radius: 40px;
}
.headingContent {
  background: #fff;
  padding: 1rem 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.blueText {
  color: #007bff;
}
.sectionHeader h5 {
  font-size: 18px;
  font-weight: 600;
}
.sectionHeader h1 {
  font-size: 60px;
  font-weight: 600;
}
.headerContent {
  background: linear-gradient(135deg, #fff 100%, #fff3f3 100%);
  gap: 2rem;
  width: 81.3vw;
  margin: auto;
  padding: 25px 25px 0 34px;
  display: flex;
  height: 170px;
}
.facilitiesContainer {
  background: #f6f6f6;
}
.straightLine {
  height: 100%;
  width: 1px;
  background-color: #16344e;
  margin-left: 15px;
  display: block;
}
.headerContent h1 {
  font-size: 60px;
  font-weight: 700;
}
.headerContent h5 {
  font-size: 18px;
  font-weight: 700;
}
.mobileContainer {
  display: none;
}
.facilitiesContainer {
  display: block;
}
@media (min-width: 1090px) and (max-width: 1199px) {
  .headerContent {
    width: 85.1vw;
  }
}
@media (min-width: 1200px) and (max-width: 1535px) {
  .headerContent {
    width: 83.9vw;
  }
}
@media (min-width: 1536px) and (max-width: 1689px) {
  .headerContent {
    width: 82.6vw;
  }
}
@media (min-width: 1690px) and (max-width: 1919px) {
  .headerContent {
    width: 81.8vw;
  }
}
@media (min-width: 2020px) and (max-width: 2309px) {
  .headerContent {
    width: 80.8vw;
  }
}
@media (min-width: 2310px) and (max-width: 2690px) {
  .headerContent {
    width: 80.2vw;
  }
}
@media (min-width: 2691px) and (max-width: 2890px) {
  .headerContent {
    width: 80.4vw;
  }
}
@media (min-width: 2691px) and (max-width: 3140px) {
  .headerContent {
    width: 79.6vw;
  }
}
@media (min-width: 3141px) and (max-width: 3440px) {
  .headerContent {
    width: 79.8vw;
  }
}
@media (min-width: 3441px) and (max-width: 3850px) {
  .headerContent {
    width: 78.8vw;
  }
}

@media (min-width: 768px) and (max-width: 1040px) {
  .headerContent h1 {
    font-size: 25px;
  }
  .headerContent h5 {
    font-size: 12px;
  }
  .headerContent {
    width: 86.7vw;
    height: 110px;
  }
  .bannerContent h2 {
    font-size: 20px;
  }
  .bannerContent p {
    font-size: 14px;
  }
  .bannerContent svg {
    font-size: 18px;
  }
  .bannerContent {
    left: 16%;
  }
}
@media (max-width: 768px) {
  .mobileContainer {
    display: block;
    background: #16344e;
    padding: 5rem 0;
  }
  .facilitiesContainer {
    display: none;
  }
  .cardImageContainer {
    position: relative;
    width: 47%;
  }
  .cardContent {
    color: #fff;
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
  }
  .cardOverlay {
    background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.75),
      rgba(0, 0, 0, 0.4) 15%,
      rgba(0, 0, 0, 0) 80%
    );
    height: 100%;
    top: 0;
    position: absolute;
    width: 100%;
    border-radius: 8px;
  }
  .cardSection {
    flex-wrap: wrap;
    display: flex;
    padding: 1rem 0.8rem;
    gap: 13px;
  }
  .cardImageContainer img {
    height: 191px;
    border-radius: 8px;
  }
  .mobileContainer p {
    font-size: 14px;
    color: #16344e;
    font-weight: 600;
  }
  .showOnlyMobileCard {
    background: #fff;
    border-radius: 8px;
    flex-flow: column wrap;
    justify-content: space-between;
    width: 47%;
    display: flex;
    position: relative;
  }
  .showOnlyMobileCard a {
    height: 191px;
  }
  .showOnlyMobileCard p {
    color: #16344e;
    font-size: 14px;
    margin-bottom: 5px;
    font-weight: 400;
  }
  .showOnlyMobileCard h1 {
    font-size: 48px;
  }
  .showOnlyMobileCard div {
    padding: 0.5rem 1rem 0;
  }
  .showOnlyMobileCard h5 {
    color: #16344e;
    font-size: 14px;
    font-weight: 400;
  }
  .mobileCardsContainer {
    background: #f6f6f6;
    padding-top: 2.5rem;
  }
}
